@font-face {
  font-family: "PT Mono";
  src: url("../fonts/ptmono-regular.woff2") format("woff2"), url("../fonts/ptmono-regular.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: "Roboto Condensed";
  src: url("../fonts/roboto-condensed-regular.woff2") format("woff2"), url("../fonts/roboto-condensed-regular.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: "Roboto Condensed";
  src: url("../fonts/roboto-condensed-bold.woff2") format("woff2"), url("../fonts/roboto-condensed-bold.woff") format("woff");
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}

html {
  box-sizing: border-box;
  height: 100%;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  border: 0 none;
  clip-path: inset(100%);
}

.about {
  position: relative;
  color: #666666;
}

.about::before {
  content: "";
  position: absolute;
  top: 5px;
  left: 0;
  width: 220px;
  height: 317px;
  background-image: url("../img/others/2005.png");
  background-repeat: no-repeat;
  background-size: 100%;
}

.about h2 {
  margin-top: 0;
  margin-bottom: 20px;
  font-size: 36px;
  line-height: 51px;
}

.about p {
  font-size: 16px;
  line-height: 22px;
}

.about p:not(:last-of-type) {
  margin-bottom: 20px;
}

.about p:last-of-type {
  margin-bottom: 40px;
}

.about__link {
  width: 180px;
}

.button {
  display: block;
  padding: 14px 28px;
  min-width: max-content;
  font-family: "Roboto Condensed", "Nimbus Sans L", "Helvetica", "Arial", sans-serif;
  font-size: 14px;
  line-height: 16px;
  font-weight: bold;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  color: #ffffff;
  text-align: center;
  border: 1px solid #ffffff;
  border-radius: 5px;
  cursor: pointer;
}

.button:visited {
  color: #ffffff;
}

.button:hover {
  border-color: #00b2ff;
}

.button:active {
  opacity: 0.5;
}

.button--blue {
  color: #00b2ff;
  border-color: #00b2ff;
}

.button--blue:visited {
  color: #00b2ff;
}

.button--blue:hover {
  background-color: #00b2ff;
  color: #ffffff;
}

.button--opacity-white {
  background-color: rgba(255, 255, 255, 0.1);
}

.button--opacity-white:hover {
  background-color: rgba(255, 255, 255, 0.5);
}

.button--opacity-white:active {
  border-color: #1b2d37;
}

.contacts-link {
  display: flex;
  justify-content: flex-end;
  width: 100%;
  flex-wrap: wrap;
  row-gap: 5px;
  column-gap: 88px;
  list-style: none;
}

.contacts-link__item {
  min-width: max-content;
  font-family: "PT Mono", "Nimbus Mono L", "Courier New", monospace;
  font-size: 16px;
  line-height: 18px;
  color: #ffffff;
}

.contacts-link__item a {
  display: flex;
  align-items: center;
  color: inherit;
}

.contacts-link__item a svg {
  fill: #00b2ff;
}

.contacts-link__item a:visited {
  color: inherit;
}

.contacts-link__item a:hover {
  color: #00b2ff;
  fill: #ffffff;
}

.contacts-link__item a:active {
  opacity: 0.5;
}

.contacts-link__item--mail svg {
  margin-right: 20px;
  width: 19px;
  height: 14px;
}

.contacts-link__item--telephone svg {
  margin-right: 16px;
  width: 18px;
  height: 18px;
}

.contacts {
  font-size: 15px;
  line-height: 25px;
}

.contacts p {
  margin-top: 0;
  margin-bottom: 0;
  opacity: 0.5;
}

.contacts a {
  display: block;
}

.features-block ul {
  margin-top: 0;
  margin-bottom: 0;
  padding-left: 0;
  position: relative;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  column-gap: 30px;
  row-gap: 30px;
  justify-content: center;
  list-style: none;
  background-color: transparent;
}

.features-block ul::before {
  content: "";
  position: absolute;
  width: 636px;
  height: 636px;
  background-image: url("../img/triangles/single-ellipse-grey.svg");
  background-repeat: no-repeat;
  z-index: -5;
  top: 32%;
  left: 39%;
}

.features-block ul::after {
  content: "";
  position: absolute;
  width: 636px;
  height: 636px;
  background-image: url("../img/triangles/single-ellipse-grey.svg");
  background-repeat: no-repeat;
  z-index: -5;
  bottom: -416px;
  left: -704px;
  transform: rotate(180deg);
}

.features-block h3 {
  margin-bottom: 17px;
  font-size: 24px;
  line-height: 34px;
  color: #00b2ff;
}

.features-block p {
  margin-bottom: 0;
  font-size: 16px;
  line-height: 22px;
  color: #666666;
}

.features {
  padding: 40px 37px 10px 40px;
  min-height: 299px;
  background-color: #ffffff;
  border: 1px solid #ebebeb;
  border-left: 3px solid #00b2ff;
}

.features:first-child {
  position: relative;
}

.features:first-child::before {
  content: "";
  position: absolute;
  width: 298px;
  height: 298px;
  background-image: url("../img/triangles/single-grey.svg");
  background-repeat: no-repeat;
  z-index: -5;
  left: -173px;
  top: -68px;
}

.features:last-child {
  position: relative;
}

.features:last-child::before {
  content: "";
  position: absolute;
  width: 298px;
  height: 298px;
  background-image: url("../img/triangles/single-grey.svg");
  background-repeat: no-repeat;
  z-index: -5;
  right: -347px;
  bottom: 17px;
  transform: rotate(180deg);
}

.features svg {
  margin-bottom: 16px;
  width: 35px;
  height: 35px;
  fill: #00b2ff;
}

.features--promptness svg {
  width: 29px;
}

.features--confidentiality svg {
  margin-bottom: 19px;
  width: 36px;
  height: 31px;
}

.features:hover {
  background-color: #00b2ff;
}

.features:hover h3,
.features:hover p {
  color: #ffffff;
}

.features:hover svg {
  fill: #ffffff;
}

.form-block {
  position: relative;
  padding-top: 91px;
  min-height: 670px;
  color: #ffffff;
  background-color: #00b2ff;
}

.form-block h2 {
  margin-bottom: 10px;
  font-weight: normal;
  font-size: 36px;
  line-height: 51px;
}

.form-block p {
  margin-bottom: 49px;
  font-size: 16px;
  line-height: 22px;
}

.form-block__container {
  position: relative;
  padding-left: 15px;
  width: 50%;
}

.form-block__container::before {
  content: "";
  position: absolute;
  width: 31%;
  height: 234px;
  background-image: url("../img/triangles/single-white.svg");
  background-repeat: no-repeat;
  right: -47%;
  top: 0;
}

.form-block__container::after {
  content: "";
  position: absolute;
  width: 48.2%;
  height: 288px;
  background-image: url("../img/triangles/single-white.svg");
  background-repeat: no-repeat;
  background-size: cover;
  transform: rotate(300deg);
  bottom: -71px;
  right: -63%;
}

.form {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  column-gap: 30px;
  row-gap: 40px;
  max-width: 100%;
}

.form input,
.form textarea {
  padding: 14px 16px;
  width: 100%;
  font-family: "PT Mono", "Nimbus Mono L", "Courier New", monospace;
  font-size: 16px;
  line-height: 22px;
  color: #666666;
  background-color: rgba(255, 255, 255, 0.1);
  border-style: none;
}

.form input:hover,
.form textarea:hover {
  background-color: rgba(255, 255, 255, 0.5);
}

.form input:focus,
.form textarea:focus {
  outline: none;
  background-color: rgba(255, 255, 255, 0.1);
  border: 1px solid #ffffff;
}

.form input::placeholder,
.form textarea::placeholder {
  color: #ffffff;
}

.form input::-webkit-input-placeholder,
.form textarea::-webkit-input-placeholder {
  color: #ffffff;
}

.form input::-moz-placeholder,
.form textarea::-moz-placeholder {
  color: #ffffff;
}

.form input {
  height: 50px;
}

.form textarea {
  grid-column: 1/-1;
  height: 154px;
  resize: none;
}

.form--modal {
  display: flex;
  flex-direction: column;
  row-gap: 0;
}

.form--modal input,
.form--modal textarea {
  padding: 11px 27px;
}

.form--modal input {
  margin-bottom: 32px;
}

.form--modal textarea {
  margin-bottom: 28px;
  height: 104px;
}

.form--modal button {
  margin-bottom: 20px;
}

.form__button {
  margin-top: 5px;
  height: 45px;
}

.form__button:focus {
  outline: 1px solid #ffffff;
}

.form__checkbox-block {
  display: flex;
  padding-top: 12px;
  padding-left: 8px;
  font-family: "PT Mono", "Nimbus Mono L", "Courier New", monospace;
  font-size: 14px;
  line-height: 16px;
  cursor: pointer;
}

.form__checkbox-block:hover {
  background-color: rgba(255, 255, 255, 0.5);
}

.form__checkbox-block input {
  appearance: none;
  margin-top: 0;
  margin-bottom: 0;
  margin-left: 0;
  margin-right: 16px;
  padding: 0;
  width: 16px;
  min-width: 16px;
  height: 16px;
  border: 1px solid #ffffff;
  cursor: pointer;
}

.form__checkbox-block input + label {
  cursor: pointer;
}

.form__checkbox-block input:checked + label {
  position: relative;
}

.form__checkbox-block input:checked + label::before {
  content: "";
  position: absolute;
  top: 5px;
  left: -29px;
  width: 9px;
  height: 6px;
  background-image: url("../img/icon/checkbox.svg");
  background-repeat: no-repeat;
}

.form__checkbox-block input:focus + label {
  position: relative;
}

.form__checkbox-block input:focus + label::after {
  content: "";
  position: absolute;
  top: -1px;
  left: -35px;
  width: 100%;
  height: 100%;
  border: 1px solid #ffffff;
}

.form__checkbox-block--modal {
  padding-left: 0;
  width: 90%;
}

.form__checkbox-block--modal input {
  margin-right: 33px;
}

.form__checkbox-block--modal input:checked + label::before {
  left: -45px;
}

.head {
  position: relative;
  color: #ffffff;
}

.head::before {
  content: "";
  position: absolute;
  height: 503px;
  background-repeat: no-repeat, no-repeat;
}

.head h1 {
  margin-top: 0;
  margin-bottom: 29px;
  width: 51%;
  font-family: "Roboto Condensed", "Nimbus Sans L", "Helvetica", "Arial", sans-serif;
  font-weight: bold;
  font-size: 80px;
  line-height: 94px;
  letter-spacing: 0.07em;
  text-transform: uppercase;
}

.head p {
  margin-top: 0;
  margin-bottom: 43px;
  width: 50%;
  font-family: "PT Mono", "Nimbus Mono L", "Courier New", monospace;
  font-size: 22px;
  line-height: 34px;
}

.head p::before {
  content: "";
  position: absolute;
  left: 0;
  top: 16px;
  width: 66px;
  height: 2px;
  background-color: #ffffff;
}

.head__content {
  position: relative;
  padding-left: 100px;
}

.head__link {
  display: block;
  padding-top: 17px;
  width: 407px;
  height: 55px;
  font-family: "Roboto Condensed", "Nimbus Sans L", "Helvetica", "Arial", sans-serif;
  font-weight: bold;
  font-size: 16px;
  line-height: 19px;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  background-color: #00b2ff;
  color: #ffffff;
  text-align: center;
  border-radius: 5px;
}

.head__link:hover {
  background-color: transparent;
  border: 1px solid #ffffff;
}

.head__link:active {
  opacity: 0.5;
}

.head__link-mobile {
  display: none;
}

.modal {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  margin: auto;
  padding-top: 29px;
  padding-left: 39px;
  padding-right: 39px;
  width: 490px;
  height: 605px;
  max-width: 100%;
  max-height: 100%;
  color: #ffffff;
  background-color: #00b2ff;
  z-index: 10;
  overflow: auto;
}

.modal h2 {
  margin-bottom: 10px;
  font-family: "Roboto Condensed", "Nimbus Sans L", "Helvetica", "Arial", sans-serif;
  font-size: 28px;
  line-height: 39px;
  font-weight: normal;
}

.modal p {
  margin-bottom: 35px;
  width: 90%;
  font-family: "PT Mono", "Nimbus Mono L", "Courier New", monospace;
  font-size: 16px;
  line-height: 22px;
}

.modal__button {
  position: absolute;
  top: 20px;
  right: 20px;
  width: 17px;
  height: 17px;
  background-color: transparent;
  background-image: url("../img/icon/close.svg");
  background-repeat: no-repeat;
  border-style: none;
}

.navigation {
  column-count: 2;
  font-size: 15px;
  line-height: 25px;
  list-style: none;
  column-gap: 31%;
}

.navigation a {
  color: inherit;
  opacity: 0.5;
}

.navigation a:visited {
  color: inherit;
}

.navigation a:hover {
  color: #00b2ff;
}

.navigation a:active {
  color: inherit;
  opacity: 1;
}

.navigation__column-list {
  padding-left: 0;
  list-style: none;
}

.navigation__item {
  min-width: max-content;
}

.page-body {
  display: grid;
  grid-template-rows: min-content 1fr min-content;
  margin: 0 auto;
  min-height: 100%;
  text-size-adjust: none;
  -moz-text-size-adjust: none;
  -webkit-text-size-adjust: none;
}

.page-body--opened-modal {
  overflow: hidden;
}

.page-body--opened-modal::before {
  content: "";
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  z-index: 5;
}

.page-footer {
  font-family: "PT Mono", "Nimbus Mono L", "Courier New", monospace;
  color: #1b2d37;
}

.page-footer h2 {
  margin-top: 0;
  margin-bottom: 26px;
  font-family: "Roboto Condensed", "Nimbus Sans L", "Helvetica", "Arial", sans-serif;
  font-size: 15px;
  line-height: 16px;
  letter-spacing: 0.05em;
  text-transform: uppercase;
}

.page-footer a:not(.page-footer__logo) {
  color: inherit;
  opacity: 0.5;
}

.page-footer a:not(.page-footer__logo):visited {
  color: inherit;
}

.page-footer a:not(.page-footer__logo):hover {
  color: #00b2ff;
}

.page-footer a:not(.page-footer__logo):active {
  color: inherit;
  opacity: 1;
}

.page-footer__top-container {
  margin-bottom: 15px;
  padding-bottom: 55px;
  width: 100%;
  border-bottom: 1px solid rgba(98, 105, 98, 0.2);
}

.page-footer__container {
  display: grid;
  grid-template-columns: 223px 1fr 333px;
  margin: 0 auto;
  padding-top: 65px;
  padding-left: 30px;
  padding-right: 30px;
  max-width: 1470px;
}

.page-footer__logo {
  display: block;
  margin-bottom: 48px;
}

.page-footer__logo img {
  width: 216px;
  height: 52px;
}

.page-footer__copyright-top {
  display: none;
}

.page-footer__social {
  margin-top: 0;
  margin-bottom: 0;
  margin-right: 0;
  padding-left: 0;
}

.page-footer__navigation {
  display: flex;
  justify-content: center;
}

.page-footer__navigation-content {
  min-width: 54%;
  padding-left: 6px;
}

.page-footer__navigation-list {
  margin-top: 0;
  margin-bottom: 0;
  padding-left: 0;
}

.page-footer__bottom-container {
  display: grid;
  grid-template-columns: 156px 233px 20.5% 1fr;
  column-gap: 4.8%;
  row-gap: 10px;
  padding-left: 30px;
  padding-right: 30px;
  margin: 0 auto;
  max-width: 1470px;
  font-size: 13px;
  line-height: 24px;
  letter-spacing: 0.04em;
  text-transform: uppercase;
}

.page-footer__bottom-container p {
  margin-top: 0;
  margin-bottom: 0;
  opacity: 0.5;
}

.page-footer__privacy {
  justify-self: flex-end;
}

.page-footer__developer {
  margin-left: auto;
}

.page-header {
  position: relative;
  margin-bottom: 37px;
  padding-top: 33px;
  background-image: url("../img/others/head-waves.png"), linear-gradient(#1b2d37);
  background-repeat: no-repeat;
  background-size: cover;
  overflow: hidden;
}

.page-header__logo {
  margin-right: 30px;
  flex-shrink: 0;
}

.page-header__logo img {
  width: 202px;
  height: 52px;
}

.page-header__link {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 0 auto 225px;
  padding-left: 30px;
  padding-right: 30px;
  max-width: 1470px;
}

.page-header__contacts {
  margin: 0;
  margin-right: 50px;
  margin-bottom: 0;
  padding-left: 0;
  margin-left: auto;
}

.page-header__section {
  margin: 0 auto;
  padding-left: 24px;
  padding-right: 24px;
  padding-bottom: 278px;
  max-width: 1470px;
}

.page-main {
  padding-top: 72px;
  overflow: hidden;
}

.page-main h2,
.page-main h3 {
  margin-top: 0;
  font-family: "Roboto Condensed", "Nimbus Sans L", "Helvetica", "Arial", sans-serif;
  font-weight: normal;
}

.page-main p {
  margin-top: 0;
  font-family: "PT Mono", "Nimbus Mono L", "Courier New", monospace;
}

.page-main__section-center {
  margin: 0 auto 105px;
  padding-left: 30px;
  padding-right: 30px;
  max-width: 1230px;
}

.page-main__section-center--about {
  padding-left: 209px;
}

.page-main__section-center--flex {
  display: flex;
  justify-content: flex-end;
}

.page-main__section-center--form {
  position: relative;
}

.page-main__section-center--form::before {
  content: "";
  position: absolute;
  left: -170px;
  bottom: -27px;
  width: 677.75px;
  height: 677.75px;
  background-image: url("../img/others/airplane.png"), url("../img/triangles/double-ellipse-white.png");
  background-repeat: no-repeat;
  background-size: 74%, 100%;
  background-position: 100% 65%, bottom;
}

.page-main__section-full {
  padding-left: 0;
  padding-right: 0;
  width: 100%;
}

.page-main__section-full--service {
  padding-top: 93px;
}

.service {
  min-height: 942px;
  font-family: "Roboto Condensed", "Nimbus Sans L", "Helvetica", "Arial", sans-serif;
  color: #ffffff;
  background-color: #1b2d37;
}

.service h2 {
  margin-bottom: 50px;
  font-size: 36px;
  line-height: 51px;
}

.service h3 {
  margin-bottom: 0;
  width: 50%;
  font-size: 20px;
  line-height: 30px;
}

.service a {
  display: block;
  padding-top: 50px;
  padding-left: 44px;
  width: 100%;
  height: 100%;
}

.service a:visited {
  color: inherit;
}

.service a:hover {
  outline: 1px solid #00b2ff;
}

.service a:active {
  opacity: 0.2;
}

.service ul {
  display: grid;
  grid-template-columns: repeat(3, 32%);
  column-gap: 27px;
  margin-top: 0;
  margin-bottom: 0;
  padding-left: 0;
  list-style: none;
}

.service__item {
  position: relative;
  max-width: 370px;
  height: 620px;
  background-repeat: no-repeat;
  background-size: cover;
}

.service__item:not(:last-child) {
  margin-bottom: 45px;
}

.service__item--production {
  background-image: url("../img/service/production.png"), linear-gradient(#1b2d37);
}

.service__item--assembly {
  background-image: url("../img/service/assembly.png"), linear-gradient(#1b2d37);
}

.service__item--component {
  background-image: url("../img/service/components.png"), linear-gradient(#1b2d37);
}

.service__item::before {
  content: "";
  position: absolute;
  bottom: 0;
  right: 0;
  width: 63px;
  height: 63px;
  background-image: url("../img/icon/arrow.svg"), linear-gradient(#00b2ff);
  background-repeat: no-repeat;
  background-position: center;
}

.social {
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-start;
  list-style: none;
}

.social a {
  display: block;
  fill: #999999;
}

.social a:visited {
  fill: #999999;
}

.social a:hover {
  fill: #00b2ff;
}

.social a:active {
  fill: #999999;
  opacity: 0.5;
}

.social__item:not(:last-child) {
  margin-right: 15px;
}

@media (min-width: 1024px) {
  .head::before {
    top: -10%;
    right: -2%;
    width: 48%;
    background-image: url("../img/head/circuit-boards.png"), url("../img/triangles/double-ellipse-white.png");
    background-position: 163% 26%, bottom;
    background-size: 98%, 96%;
